<!--eslint-disable -->
<template>
  <div id="pesEditModal" ref="pesEditModal" class="modal fade" tabindex="-1" aria-labelledby="pesEditModalLabel" aria-hidden="true">
    <div class="modal-dialog modal-xl">
      <v-form class="modal-content border-0" v-slot="{ errors }">
        <div class="modal-header bg-dark text-white">
          <h5 id="pesEditModalLabel" class="modal-title">
            <span></span>
          </h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm">
              <div class="row mb-3">
                <div class="form-group col-md-4">
                  <label for="diagnosis_no"></label>
                  <v-field id="diagnosis_no" name="" type="number" v-model="infoDetail.diagnosis_no" :rules="{ required: true, min: 1, min_value: 1 }" :class="{'is-invalid': errors['']}" disabled></v-field>
                  <error-message name="" class="invalid-feedback"></error-message>
                </div>
                <div class="form-group col-md-4">
                  <label for="patient_id"></label>
                  <v-field id="patient_id" name="" type="number" v-model="infoDetail.patient_id" :rules="{ required: true, min: 1, max: 7, min_value: 1 }" :class="{'is-invalid': errors['']}" disabled></v-field>
                  <error-message name="" class="invalid-feedback"></error-message>
                </div>
                <div class="form-group col-md-4">
                  <label for="patient_name"></label>
                  <v-field id="patient_name" name="" type="text" v-model="infoDetail.name" :rules="{ required: true, max: 10 }" :class="{'is-invalid': errors['']}" disabled></v-field>
                  <error-message name="" class="invalid-feedback"></error-message>
                </div>
              </div>
              <div class="form-textarea mb-4">
                <div class="form-label">
                  <label for="problem">Problem</label>
                </div>
                <textarea id="problem" type="text" name="textarea" class="form-control" rows="10" cols="30" v-model="infoDetail.problem" required></textarea>
              </div>
              <div class="form-textarea mb-4">
                <div class="form-label">
                  <label for="etiology">Etiology</label>
                </div>
                <textarea id="etiology" type="text" name="textarea" class="form-control" rows="10" cols="30" v-model="infoDetail.etiology" required></textarea>
              </div>
              <div class="form-textarea mb-4">
                <div class="form-label">
                  <label for="symptoms">Symptoms</label>
                </div>
                <textarea id="etiology" type="text" name="textarea" class="form-control" rows="10" cols="30" v-model="infoDetail.symptoms" required></textarea>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal"></button>
          <button type="button" class="btn btn-primary" @click="confirmEdit"></button>
        </div>
      </v-form>
    </div>
  </div>
</template>

<script>
import { Field as VField, Form as VForm, ErrorMessage, defineRule, configure } from 'vee-validate';
//  VeeValidate 
import AllRules from '@vee-validate/rules';
//  VeeValidate  i18n 
import { localize, setLocale } from '@vee-validate/i18n';
//  VeeValidate 
import zhTW from '@vee-validate/i18n/dist/locale/zh_TW.json';
import { mapGetters } from 'vuex';
import { customizeSuccessMsg, customizeErrorMsg } from '../views/toastMessage';

//  Object.keys  AllRules  forEach  VeeValidate
Object.keys(AllRules).forEach((rule) => {
  defineRule(rule, AllRules[rule]);
});
//  VeeValidate 
configure({
  generateMessage: localize({ zh_TW: zhTW }),
  validateOnInput: true,
});
setLocale('zh_TW');

export default {
  data() {
    return {
      fixApi: 'https://140.123.173.4',
    };
  },
  methods: {
    confirmEdit() {
      this.$store.dispatch('startLoading');
      const api = `${this.fixApi}/nutritionist/nutritionalAssessmentDiagnosis/${this.nutritionist_id}/${this.infoDetail.diagnosis_no}`;
      this.$http.put(api, this.infoDetail).then((res) => {
        this.$emit('editPes');
        console.log(res.data);
        setTimeout(() => {
          this.$store.dispatch('stopLoading');
        }, 350);
        customizeSuccessMsg(res.data.msg);
      }).catch((err) => {
        console.log(err);
        customizeErrorMsg(err.response.data.msg);
      });
    },
  },
  computed: {
    ...mapGetters(['getUserID']), //  Vuex getter
    nutritionist_id() {
      //  getter  Vuex  state 
      return this.getUserID;
    },
  },
  props: {
    infoDetail: {
      default: {},
    },
  },
  emits: ['editPes'], // 
  components: {
    VField,
    VForm,
    ErrorMessage,
  },
};
</script>

<style lang="scss">
  .btn-close {
    background-color: white;
  }
  .form-label {
    display: flex;
    justify-items: top;
  }
</style>
